import React, { Component } from 'react'
import { withRouter } from 'react-router'
import { ListView } from 'antd-mobile'

import * as orderActions from '../../store/actions/orderActions'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'

import OrderItem from '@/components/OrderItem/OrderItem'
import './orderList.scss'

class OrderList extends Component {
  constructor(props) {
    super(props)

    this.state = {
      pageNo: 1,
      pageSize: 10,
      isLoading: false, // 
      hasMoreOrder: true // 
    }
    this.getOrderList = this.getOrderList.bind(this)
  }

  componentWillMount() {
    this.getOrderList()
  }

  // 
  async getOrderList() {

    let pageNo = this.state.pageNo
    const pageSize = this.state.pageSize
    //  || 
    if (this.state.isLoading || !this.state.hasMoreOrder) {
      return false
    }
    this.setState({
      isLoading: true
    })
    const { data } = await this.props.orderActions.getOrderList(pageNo, pageSize)
    this.setState({
      isLoading: false
    })
    if (data.hasMoreOrder) {
      this.setState({
        pageNo: ++pageNo
      })
    } else {
      // 
      this.setState({
        hasMoreOrder: false
      })
    }
  }


  render() {
    const dataSource = new ListView.DataSource({
      rowHasChanged: (row1, row2) => row1 !== row2,
    })
    const orderList = dataSource.cloneWithRows(this.props.orderList)

    return (
      <div className="order-list-page">
        {
          this.props.orderList.length ?
            <ListView
              dataSource={orderList} // 
              renderRow={OrderItem} // 
              initialListSize={10} // 
              scrollRenderAheadDistance={500} // 
              onEndReached={this.getOrderList} // 
              pageSize={this.state.pageSize}
              useBodyScroll
              renderFooter={() => (<div style={{ padding: 30, textAlign: 'center' }}>
                {this.state.hasMoreOrder ? '...' : ''}
              </div>)}
            />
            :
            <div className="no-order"></div>
        }
        <div className="activity-btn"></div>
      </div>
    )
  }
}

const mapStateToProps = state => {
  return {
    orderList: state.orderReducer.orderList
  }
}

const mapDispatchToProps = dispatch => {
  return {
    orderActions: bindActionCreators(orderActions, dispatch)
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(OrderList))